import * as XLSX from "xlsx";
// JSONDatajson,fileName,title,filter,rowLength
export function JSONToExcelConvertor(JSONData, FileName) {
  const title = [
    "",
    "",
    "",
    "emt",
    "",
    "ID",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  if (!JSONData) {
    return;
  }
  // jsonobject
  var arrData = typeof JSONData !== "object" ? JSON.parse(JSONData) : JSONData;

  var excel = '<table id="expoetTable">';

  // 
  var row = "<tr>";
  if (title) {
    // 
    for (let i = 0; i < title.length; i++) {
      row += "<th align='center'>" + title[i] + "</th>"; // row
    }
  } else {
    // 
    for (var i in arrData[0]) {
      row += "<th align='center'>" + i + "</th>";
    }
  }
  excel += row + "</tr>";
  row = "";
  // 
  for (let index1 = 0; index1 < arrData.length; index1++) {
    // row = "<tr>"
    for (var index in arrData[index1]) {
      let value1;
      if (index == "vital" || index == "selectedParts") {
        value1 =
          arrData[index1][index] == null
            ? ""
            : JSON.stringify(arrData[index1][index]);
      } else {
        value1 = arrData[index1][index] == null ? "" : arrData[index1][index];
      }
      // eslint-disable-next-line quotes
      row += "<td align='center'>" + value1 + "</td>";
    }
    excel += "<tr>" + row + "</tr>";
    row = "";
  }

  excel += "</table>";
  var objE = document.createElement("div"); // string,js-xlsxdom,divinnerHTML,childNodes[0]dom
  objE.innerHTML = excel;
  var sheet = XLSX.utils.table_to_sheet(objE.childNodes[0], { raw: true }); // tablesheet,rawtruestring,
  openDownloadDialog(sheet2blob(sheet, FileName), FileName + ".xlsx");
}

// sheetexcelblobURL.createObjectURL
function sheet2blob(sheet, sheetName) {
  sheetName = sheetName || "sheet1"; // sheetNamesheet1
  var workbook = {
    SheetNames: [sheetName],
    Sheets: {},
  };
  workbook.Sheets[sheetName] = sheet; // excel

  var wopts = {
    bookType: "xlsx", // 
    bookSST: false, // Shared String TableIOS
    type: "binary", // 
  };
  var wbout = XLSX.write(workbook, wopts);
  var blob = new Blob([s2ab(wbout)], {
    type: "application/octet-stream",
  }); // ArrayBuffer
  function s2ab(s) {
    var buf = new ArrayBuffer(s.length);
    var view = new Uint8Array(buf);
    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xff;
    return buf;
  }
  return blob;
}
// 
function openDownloadDialog(url, saveName) {
  if (typeof url === "object" && url instanceof Blob) {
    url = URL.createObjectURL(url); // blob
  }
  var aLink = document.createElement("a");
  aLink.href = url;
  aLink.download = saveName || ""; // HTML5file:///
  var event;
  if (window.MouseEvent) event = new MouseEvent("click");
  else {
    event = document.createEvent("MouseEvents");
    event.initMouseEvent(
      "click",
      true,
      false,
      window,
      0,
      0,
      0,
      0,
      0,
      false,
      false,
      false,
      false,
      0,
      null
    );
  }
  aLink.dispatchEvent(event);
}
